import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CrmService } from 'src/app/core/services/crm.service';

@Component({
  selector: 'app-activity',
  templateUrl: './activity.component.html',
  styleUrls: ['./activity.component.scss']
})
export class ActivityComponent implements OnInit {
  public cardData =[
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"04/08/2023",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
      {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
]

public editleadId: string = "";
public name:any;
public currentUser = JSON.parse(localStorage.getItem("currentUser"));
public currentPageNo: number = 0;
  public partyPageSize: number = 10;
  public activitylistData:any;
  public searchKey: any;

  constructor( public crmservice: CrmService,
    private route: ActivatedRoute,) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe((p) => {
      this.editleadId = p.get("id");
    });
    console.log("this.leadId",this.editleadId)

    this.geteditLeadById();
    this.getactivityList();
  }

  geteditLeadById() {
    this.crmservice.getleadById({}, this.editleadId).then((res) => {
      console.log("res activity",res);
      this.name=res.data.name;
    })
  }

  getactivityList(){
    this.crmservice.getActivityList({},this.editleadId).then((res) => {
      console.log("view active list data",res);
      this.activitylistData = res.data;

    })
  }

  getsearchactivityList(searchKey){
    this.crmservice.getActivityListsearch({},this.editleadId,this.searchKey).then((res) => {
      console.log("view active list data search",res);
      this.activitylistData = res.data;

    })
  }
}
