import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
// ckeditor
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
import { DomSanitizer } from '@angular/platform-browser';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { CrmService } from 'src/app/core/services/crm.service';
import { TitleCasePipe } from '@angular/common';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { CommonService } from 'src/app/core/services/common.service';
const editorContent = ``;

let editorConfig = {
  ui: 'pt',
  // language: 'pt',
  toolbar: {
    items: [
      'alignment',  // Displaying the proper UI element in the toolbar.
      'heading',
      '|',
      'bold',
      'italic',
      'link',
      'bulletedList',
      'numberedList',
      '|',
      'outdent',
      'indent',
      '|',
      'imageUpload',
      'blockQuote',
      'insertTable',
      'mediaEmbed',
      'undo',
      'redo'
    ]
  },
  language: 'en',
  image: {
    toolbar: [
      'imageTextAlternative',
      'toggleImageCaption',
      'imageStyle:inline',
      'imageStyle:block',
      'imageStyle:side'
    ]
  },
  table: {
    contentToolbar: [
      'tableColumn',
      'tableRow',
      'mergeTableCells'
    ]
  }
};


@Component({
  selector: 'app-log-meeting',
  templateUrl: './log-meeting.component.html',
  styleUrls: ['./log-meeting.component.scss']
})
export class LogMeetingComponent implements OnInit {
  ckeditor = ClassicEditor;
  public leadId:any;
  editorContent = editorContent;
  editorConfig = editorConfig;
  AddlogMeetingForm:FormGroup;
    userData: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  today: Date;

  constructor(public dialogRef: MatDialogRef<LogMeetingComponent> ,@Inject(MAT_DIALOG_DATA) public logMeetingData: any
  ,private formBuilder: FormBuilder, private toastService: ToastNotificationService,private crmService:CrmService, private titleCasePipe: TitleCasePipe,
  public rolesPermissionsService: RolesPermissionsService, private commonService : CommonService) { }

  ngOnInit(): void {
    this.today=new Date();
      this.getUsersList() ;
    this.leadId=this.logMeetingData.editleadId
    console.log("leadId",this.leadId)
    this.AddlogMeetingForm = this.formBuilder.group({
      taskAssignedTo:['', Validators.required],
      taskNote:['', Validators.required],
      taskdate:['', Validators.required],
      taskReminder:['', Validators.required],
      taskType:['', Validators.required],
      task: [''],    
      taskTime:[''],  
  
     
      mettingOutComes:['']
     
    });
  }
  onChange({ editor }: ChangeEvent) {
    this.editorContent = editor.getData();
  }
  getUsersList() {
    this.rolesPermissionsService.getUsers({}, this.currentUser.id,1000,1).then((res) => {
      this.userData = res.data.pageData.map((item)=>{ return item.firstName});
      console.log(this.userData)
    })
  }
  clearInput() {
    this.dialogRef.close();
  }
  AddMeetLog(){
    if (this.AddlogMeetingForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else{
     let  Body=

     {
      "assignedTo": this.AddlogMeetingForm.controls.taskAssignedTo.value,
      "note":this.AddlogMeetingForm.controls.mettingOutComes.value,
      "actualDate": this.AddlogMeetingForm.controls.taskdate.value,
       "remainder": this.AddlogMeetingForm.controls.taskReminder.value,
      "taskDescription": this.AddlogMeetingForm.controls.taskNote.value,
      "type": this.AddlogMeetingForm.controls.taskType.value,
      "images": []
     }
      this.crmService.PostMeetingLogAdd(Body,this.leadId).then(res=>{
        this.commonService.notifyDataAdded();
        this.toastService.toastMsg({
          title: "Success",
          content: "Meeting Log Added Successfully!!!"
        });
        // 
        // this.toastService.openSnackBar("Meeting Log Added Successfully!!!");
        this.dialogRef.close();
      }
      , (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
      })


    }

  }
  openDatepicker(){
    // Check if the current value of taskdate is an "Invalid Date"
const selectedDate = this.AddlogMeetingForm.get('taskdate').value;
if (isNaN(selectedDate.getTime())) {
  // Set a default date or any valid date here
  this.AddlogMeetingForm.get('taskdate').setValue(new Date());
}
}

}
