import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

// ckeditor
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
import { DomSanitizer } from '@angular/platform-browser';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CommonService } from 'src/app/core/services/common.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { CrmService } from 'src/app/core/services/crm.service';
import { TitleCasePipe } from '@angular/common';

const editorContent = ``;

let editorConfig = {
  ui: 'pt',
  // language: 'pt',
  toolbar: {
    items: [
      'alignment',  // Displaying the proper UI element in the toolbar.
      'heading',
      '|',
      'bold',
      'italic',
      'link',
      'bulletedList',
      'numberedList',
      '|',
      'outdent',
      'indent',
      '|',
      'imageUpload',
      'blockQuote',
      'insertTable',
      'mediaEmbed',
      'undo',
      'redo'
    ]
  },
  language: 'en',
  image: {
    toolbar: [
      'imageTextAlternative',
      'toggleImageCaption',
      'imageStyle:inline',
      'imageStyle:block',
      'imageStyle:side'
    ]
  },
  table: {
    contentToolbar: [
      'tableColumn',
      'tableRow',
      'mergeTableCells'
    ]
  }
};


@Component({
  selector: 'app-create-mail',
  templateUrl: './create-mail.component.html',
  styleUrls: ['./create-mail.component.scss']
})
export class CreateMailComponent implements OnInit {
  ckeditor = ClassicEditor;
  editorConfig = editorConfig;
  output = null;
  editorContent = editorContent;

  emailForm: FormGroup;
  editleadId: any;
  public leadId: any;


  constructor(public dialogRef: MatDialogRef<CreateMailComponent>, private formBuilder: FormBuilder,
    private commonService: CommonService, private toastService: ToastNotificationService, private crmService: CrmService, private titleCasePipe: TitleCasePipe) { }

  ngOnInit(): void {

    this.emailForm = this.formBuilder.group({
      sendto: ['', Validators.required],
      subject: ['', Validators.required],
      html: ['', Validators.required],
    });
  }
  clearInput() {
    this.dialogRef.close();
  }
  onChange({ editor }: ChangeEvent) {
    this.editorContent = editor.getData();
  }

  send() {
    if (this.emailForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {

      let Body =
      {
        "userId": JSON.parse(localStorage.getItem("currentUser")).id,
        "sendto": this.emailForm.controls.sendto.value,
        "subject": this.emailForm.controls.subject.value,
        "html": this.emailForm.controls.html.value,
      }


      this.crmService.sendEmail(Body).then(res => {
        this.commonService.notifyDataAdded()
        this.toastService.toastMsg({
          title: "Success",
          content: "Log Mail Added Successfully!!!"
        });
        // this.toastService.openSnackBar("Log Mail Added Successfully!!!");
        this.dialogRef.close();

      }
        , (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
            // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
          }
          else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went Wrong",
            });
            // this.toastService.openErrorSnackBar("Something Went Wrong.");
          }
        })


    }

  }

}
