import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { CrmService } from 'src/app/core/services/crm.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

// ckeditor
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
import { DomSanitizer } from '@angular/platform-browser';
import { CommonService } from 'src/app/core/services/common.service';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';

const editorContent = ``;

let editorConfig = {
  ui: 'pt',
  // language: 'pt',
  toolbar: {
    items: [
      'alignment',  // Displaying the proper UI element in the toolbar.
      'heading',
      '|',
      'bold',
      'italic',
      'link',
      'bulletedList',
      'numberedList',
      '|',
      'outdent',
      'indent',
      '|',
      'imageUpload',
      'blockQuote',
      'insertTable',
      'mediaEmbed',
      'undo',
      'redo'
    ]
  },
  language: 'en',
  image: {
    toolbar: [
      'imageTextAlternative',
      'toggleImageCaption',
      'imageStyle:inline',
      'imageStyle:block',
      'imageStyle:side'
    ]
  },
  table: {
    contentToolbar: [
      'tableColumn',
      'tableRow',
      'mergeTableCells'
    ]
  }
};


@Component({
  selector: 'app-log-email',
  templateUrl: './log-email.component.html',
  styleUrls: ['./log-email.component.scss']
})
export class LogEmailComponent implements OnInit {
  ckeditor = ClassicEditor;
  editorConfig = editorConfig;
  output = null;
  editorContent = editorContent;
  AddEmailForm: FormGroup;
  editleadId: any;
  public leadId: any;
  today: Date;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  userData: any;



  constructor(public dialogRef: MatDialogRef<LogEmailComponent>, private formBuilder: FormBuilder, 
    @Inject(MAT_DIALOG_DATA) public taskData: any, private toastService: ToastNotificationService, 
    private crmService: CrmService, private titleCasePipe: TitleCasePipe, private domSanitizer: DomSanitizer,
    private commonService : CommonService,  public rolesPermissionsService: RolesPermissionsService,) { }

  ngOnInit(): void {
    this.today=new Date();
    this.leadId = this.taskData.editleadId;
    console.log("leadId", this.leadId)
    this.getUsersList() ;

    this.AddEmailForm = this.formBuilder.group({
      taskName: ['', Validators.required],
      taskdate: ['', Validators.required],
      taskTime: ['', Validators.required],
      taskReminder: [false],
      taskType: ['', Validators.required],
      taskPrioirty: [''],
      // taskQue: ['', Validators.required],
      taskAssignedTo: ['', Validators.required],
      taskNote: ['', Validators.required],
    });
  }
  getUsersList() {
    this.rolesPermissionsService.getUsers({}, this.currentUser.id,1000,1).then((res) => {
      this.userData = res.data.pageData.map((item)=>{ return item.firstName});
      console.log(this.userData)
    })
  }

  clearInput() {
    this.dialogRef.close();
  }
  //ckeditor

  onChange({ editor }: ChangeEvent) {
    this.editorContent = editor.getData();
  }

  submit() {
    this.output = this.domSanitizer.bypassSecurityTrustHtml(this.editorContent);
  }
  //ckeditor
  AddTask() {
    if (this.AddEmailForm.invalid) {
      console.log("eee",this.AddEmailForm)
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {

      let Body =
      {
        "assignedTo": this.AddEmailForm.controls.taskAssignedTo.value,
        "taskName": this.AddEmailForm.controls.taskName.value,
        "actualDate": this.AddEmailForm.controls.taskdate.value,
        "taskDescription": this.AddEmailForm.controls.taskNote.value,
        "remainder": this.AddEmailForm.controls.taskReminder.value,
        "type": this.AddEmailForm.controls.taskType.value,
        "priority": this.AddEmailForm.controls.taskPrioirty.value,
        "images": []

      }


      this.crmService.postEmail(Body, this.leadId).then(res => {
        this.commonService.notifyDataAdded()
        // this.toastService.openSnackBar("Log Mail Added Successfully!!!");
        this.toastService.toastMsg({
          title: "Success",
          content: "Log Mail Added Successfully!!!",
        });
        this.dialogRef.close();

      }
        , (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
            // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
          }
          else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went Wrong.",
            });
            // this.toastService.openErrorSnackBar("Something Went Wrong.");
          }
        })


    }

  }
  openDatepicker(){
    // Check if the current value of taskdate is an "Invalid Date"
const selectedDate = this.AddEmailForm.get('taskdate').value;
if (isNaN(selectedDate.getTime())) {
  // Set a default date or any valid date here
  this.AddEmailForm.get('taskdate').setValue(new Date());
}
}


}
