import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { CrmService } from 'src/app/core/services/crm.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
// ckeditor
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
import { DomSanitizer } from '@angular/platform-browser';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { CommonService } from 'src/app/core/services/common.service';

const editorContent = ``;

let editorConfig = {
  ui: 'pt',
  // language: 'pt',
  toolbar: {
    items: [
      'alignment',  // Displaying the proper UI element in the toolbar.
      'heading',
      '|',
      'bold',
      'italic',
      'link',
      'bulletedList',
      'numberedList',
      '|',
      'outdent',
      'indent',
      '|',
      'imageUpload',
      'blockQuote',
      'insertTable',
      'mediaEmbed',
      'undo',
      'redo'
    ]
  },
  language: 'en',
  image: {
    toolbar: [
      'imageTextAlternative',
      'toggleImageCaption',
      'imageStyle:inline',
      'imageStyle:block',
      'imageStyle:side'
    ]
  },
  table: {
    contentToolbar: [
      'tableColumn',
      'tableRow',
      'mergeTableCells'
    ]
  }
};

@Component({
  selector: 'app-add-task',
  templateUrl: './add-task.component.html',
  styleUrls: ['./add-task.component.scss']
})
export class AddTaskComponent implements OnInit {
  ckeditor = ClassicEditor;
  editorConfig = editorConfig;
  output = null;
  editorContent = editorContent;
  AddTaskForm: FormGroup;
  editleadId: any;
  public leadId:any;
  userData: any;
  public today:any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))

  constructor(public dialogRef: MatDialogRef<AddTaskComponent>,private formBuilder: FormBuilder,@Inject(MAT_DIALOG_DATA) public taskData: any, private toastService: ToastNotificationService,private crmService:CrmService,
  private commonService : CommonService,
  private titleCasePipe: TitleCasePipe,public rolesPermissionsService: RolesPermissionsService,) { }

  ngOnInit(): void {
    this.today=new Date();
    this.leadId=this.taskData.editleadId;
    console.log("leadId",this.leadId)
    this.getUsersList();

    this.AddTaskForm = this.formBuilder.group({
      task: ['', Validators.required],
      taskdate:['', Validators.required],
      taskTime:['', Validators.required],
      taskReminder:['', Validators.required],
      taskType:['', Validators.required],
      taskPrioirty:['', Validators.required],
      taskQue:['', Validators.required],
      taskAssignedTo:['', Validators.required],
      taskNote:['', Validators.required],
      remindTime:['',Validators.required]
    });
  }
  getUsersList() {
    this.rolesPermissionsService.getUsers({}, this.currentUser.id,1000,1).then((res:any) => {
      this.userData = res.data.pageData.map((item)=>{ return item.firstName});
      console.log(this.userData)
    })
  }

  clearInput() {
    this.dialogRef.close();
  }
   //ckeditor

   onChange({ editor }: ChangeEvent) {
    this.editorContent = editor.getData();
  }
  AddTask(){
    if (this.AddTaskForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else{
      const taskReminderValue = this.AddTaskForm.controls['taskReminder'].value;
      const remindTimeValue = this.AddTaskForm.controls['remindTime'].value;
  
      // Parse the date string from taskReminderValue
      const taskReminderDate = new Date(taskReminderValue);
  
      // Extract time values from remindTimeValue
      const [hours, minutes] = remindTimeValue.split(':');
  
      // Set time components of taskReminderDate
      taskReminderDate.setHours(parseInt(hours), parseInt(minutes));
  
      // Convert the combined Date object to UTC string
      const combinedDateTimeUtc = taskReminderDate.toISOString();
  
      console.log(combinedDateTimeUtc);


     let  Body=
      {
          "taskName": this.AddTaskForm.controls.task.value,
          "dueDate": this.AddTaskForm.controls.taskdate.value,
          "actualDate":this.AddTaskForm.controls.taskdate.value,
          "remainder": combinedDateTimeUtc,
          "type": this.AddTaskForm.controls.taskType.value,
          "priority": this.AddTaskForm.controls.taskPrioirty.value,
          "queue": this.AddTaskForm.controls.taskQue.value,
          "assignedTo": this.AddTaskForm.controls.taskAssignedTo.value,
          "taskDescription": this.AddTaskForm.controls.taskNote.value,
          "images": []
          
      }
      
      this.crmService.postTask(Body,this.leadId).then(res=>{
        this.commonService.notifyDataAdded()
        this.toastService.toastMsg({
          title: "Success",
          content: "Task Added Successfully!!!"
        });
        // this.toastService.openSnackBar("Task Added Successfully!!!");
        this.dialogRef.close();

      }
      , (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
      })


    }

  }
  openDatepicker() {
    // Check if the current value of taskdate is an instance of Date
    const selectedDate = this.AddTaskForm.get('taskdate').value;
    if (selectedDate instanceof Date && !isNaN(selectedDate.getTime())) {
      // If selectedDate is a valid Date object, do nothing
      return;
    } else {
      // Set a default date or any valid date here
      this.AddTaskForm.get('taskdate').setValue(new Date());
    }
  }
}
