import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { LogMeetingComponent } from '../log-meeting/log-meeting.component';
import { CrmService } from 'src/app/core/services/crm.service';
import { ActivatedRoute } from '@angular/router';
import { CreateMeetingComponent } from '../create-meeting/create-meeting.component';
import { CommonService } from 'src/app/core/services/common.service';

@Component({
  selector: 'app-meeting',
  templateUrl: './meeting.component.html',
  styleUrls: ['./meeting.component.scss']
})
export class MeetingComponent implements OnInit {
  public cardData =[
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"04/08/2023",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
      {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
]

public editleadId: string = "";
public name:any;
public search = "MEETING";
public meetinglistData:any;

  constructor(public dialog: MatDialog,
    public crmservice: CrmService, private commonService : CommonService,
    private route: ActivatedRoute,) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe((p) => {
      this.editleadId = p.get("id");
    });
    console.log("this.leadId",this.editleadId)
    this.geteditLeadById();
    this.getmeetingList();
    this.commonService.rowAdded$.subscribe(() => {
      this.getmeetingList();
    });

    
  }

  geteditLeadById() {
    this.crmservice.getleadById({}, this.editleadId).then((res) => {
      console.log("res activity",res);
      this.name=res.data.name;
    })
  }

  getmeetingList(){
    this.crmservice.getMeetingList({},this.editleadId,this.search).then((res) => {
      console.log("meeting list data",res);
      this.meetinglistData = res.data.pageData;

    })
  }

  openDialog(editleadId): void {
    const dialogRef: MatDialogRef<LogMeetingComponent> = this.dialog.open(LogMeetingComponent, {
      width: '700px',
      height: '730px',
      data: {editleadId:editleadId},
      panelClass: 'custom-dialog-logmeeting-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openDialogcreateMeeting(): void {
    const dialogRef: MatDialogRef<CreateMeetingComponent> = this.dialog.open(CreateMeetingComponent, {
      width: '',
      height: '',
      data: {},
      panelClass: 'custom-dialog-createmeeting-class',
      disableClose: true,
      // position: {
      //   right: '0',
      // },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }


}
