import { Component, OnInit } from '@angular/core';
import { LogEmailComponent } from '../log-email/log-email.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { CrmService } from 'src/app/core/services/crm.service';
import { ActivatedRoute } from '@angular/router';
import { CreateMailComponent } from '../create-mail/create-mail.component';
import { CommonService } from 'src/app/core/services/common.service';
import { UpdateEmailComponent } from '../update-email/update-email.component';

@Component({
  selector: 'app-mail',
  templateUrl: './mail.component.html',
  styleUrls: ['./mail.component.scss']
})
export class MailComponent implements OnInit {
  public cardData =[
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"04/08/2023",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
      {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
]

public editleadId: string = "";
public name:any;
public search="EMAIL";
public emailist :any;


  constructor(public dialog: MatDialog,
    public crmservice: CrmService, private commonService : CommonService,
    private route: ActivatedRoute,) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe((p) => {
      this.editleadId = p.get("id");
    });
    console.log("this.leadId",this.editleadId)
    this.geteditLeadById();
    this.getmailList();
    this.commonService.rowAdded$.subscribe(() => {
      this.getmailList();
    });
  }

  geteditLeadById() {
    this.crmservice.getleadById({}, this.editleadId).then((res) => {
      console.log("res activity",res);
      this.name=res.data.name;
    })
  }

  getmailList() {
    this.crmservice.getMailList({}, this.editleadId,this.search).then((res) => {
      console.log("res mail view list",res);
      this.emailist = res.data.pageData;
    })
  }

  openDialog(editleadId): void {
    const dialogRef: MatDialogRef<LogEmailComponent> = this.dialog.open(LogEmailComponent, {
      width: '700px',
      height: '730px',
      data: {editleadId:editleadId},
      panelClass: 'custom-dialog-logmail-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openDialogcreatemail(editleadId): void {
    const dialogRef: MatDialogRef<CreateMailComponent> = this.dialog.open(CreateMailComponent, {
      width: '700px',
      height: '730px',
      data: {editleadId:editleadId},
      panelClass: 'custom-dialog-createmail-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openMailTaskModel(editleadId,taskId){
    const dialogRef: MatDialogRef<UpdateEmailComponent> = this.dialog.open(UpdateEmailComponent, {
      width: '700px',
      height: '730px',
      data: { editleadId: editleadId,taskId:taskId },
      panelClass: 'custom-dialog-updateEmail-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }
}
