import { Component, OnInit } from '@angular/core';
import { AddNoteComponent } from '../add-note/add-note.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { CrmService } from 'src/app/core/services/crm.service';
import { ActivatedRoute } from '@angular/router';
import { UpdateNoteComponent } from '../update-note/update-note.component';
import { CommonService } from 'src/app/core/services/common.service';



@Component({
  selector: 'app-notes',
  templateUrl: './notes.component.html',
  styleUrls: ['./notes.component.scss']
})
export class NotesComponent implements OnInit {
  public cardData = [
    {
      "status": "upcoming",
      "noteby": "Note By ash More",
      "note": "This is note created by ash more",
      "date": "04/08/2023"
    },
    {
      "status": "04/08/2023",
      "noteby": "Note By ash More",
      "note": "This is note created by ash more",
      "date": "04/08/2023"
    },
    {
      "status": "upcoming",
      "noteby": "Note By ash More",
      "note": "This is note created by ash more",
      "date": "04/08/2023"
    },
    {
      "status": "upcoming",
      "noteby": "Note By ash More",
      "note": "This is note created by ash more",
      "date": "04/08/2023"
    },
  ]



  public editleadId: string = "";
  public name: any;
  public search = "NOTES";
  public notesList: any;
  public searchKey: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public currentPageNo: number = 0;
  public partyPageSize: number = 5;

  constructor(public dialog: MatDialog,
    public crmservice: CrmService, private commonService : CommonService,
    private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe((p) => {
      this.editleadId = p.get("id");
    });
    console.log("this.leadId", this.editleadId)

    this.geteditLeadById();
    this.getnotesList();
    this.commonService.rowAdded$.subscribe(() => {
      this.getnotesList();
    }); 
  }


  geteditLeadById() {
    this.crmservice.getleadById({}, this.editleadId).then((res) => {
      console.log("res", res);
      this.name = res.data.name;
    })
  }


  getnotesList() {
    this.crmservice.getNoteList({}, this.editleadId, this.search).then((res) => {
      console.log("res notes view", res);
      this.notesList = res.data.pageData;

    })
  }

  getsearchnoteList(searchKey){
    this.crmservice.getNotesListsearch({},this.editleadId,this.search,this.partyPageSize, (this.currentPageNo + 1),this.searchKey).then((res) => {
      console.log("search note",res);
      this.notesList = res.data.pageData;

    })
  }
  openEditNoteModel(editleadId,noteId){
    const dialogRef: MatDialogRef<UpdateNoteComponent> = this.dialog.open(UpdateNoteComponent, {
      width: '700px',
      height: '430px',
      data: { editleadId: editleadId,noteId:noteId },
      panelClass: 'custom-dialog-notes-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }


  openDialog(editleadId): void {
    const dialogRef: MatDialogRef<AddNoteComponent> = this.dialog.open(AddNoteComponent, {
      width: '700px',
      height: '430px',
      data: { editleadId: editleadId },
      panelClass: 'custom-dialog-notes-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

}
