import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CrmmainComponent } from './crmmain/crmmain.component';
import { LeadListComponent } from './lead-list/lead-list.component';
import { ViewLeadComponent } from './view-lead/view-lead.component';
import { ActivityComponent } from './activity/activity.component';
import { NotesComponent } from './notes/notes.component';
import { TaskComponent } from './task/task.component';
import { MeetingComponent } from './meeting/meeting.component';
import { MailComponent } from './mail/mail.component';

const routes: Routes = [
  {
    path:'',
    component:CrmmainComponent,
    children: [
      {
        path:'',
        redirectTo: 'lead-list',
        pathMatch: 'full'
      },
      {
        path:'lead-list',
        component:LeadListComponent,
      },
    ],
    // pathMatch: 'prefix',
    // data: {
    //   routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    // }
  },
  {
    path:'view-lead/:id',
    component:ViewLeadComponent,
    children: [
      {
        path:'activity/:id',
        component:ActivityComponent,
      },
      {
        path:'notes/:id',
        component:NotesComponent,
      },
      {
        path:'task/:id',
        component:TaskComponent,
      },
      {
        path:'meeting/:id',
        component:MeetingComponent,
      },
      {
        path:'mail/:id',
        component:MailComponent,
      },
    ],
   
  },
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CrmmoduleRoutingModule { }
