import { Component, OnInit } from '@angular/core';
import { AddTaskComponent } from '../add-task/add-task.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { CrmService } from 'src/app/core/services/crm.service';
import { ActivatedRoute } from '@angular/router';
import { UpdateTaskComponent } from '../update-task/update-task.component';
import { CommonService } from 'src/app/core/services/common.service';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.scss']
})
export class TaskComponent implements OnInit {
  public cardData =[
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"04/08/2023",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
    {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
      {
      "status":"upcoming",
      "noteby":"Note By ash More",
      "note":"This is note created by ash more",
      "date":"04/08/2023"
    },
]

public editleadId: string = "";
public name:any;
public search="TASK";
public tasklist:any;
public searchKey: any;
public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public currentPageNo: number = 0;
  public partyPageSize: number = 5;


  constructor(public dialog: MatDialog,
    public crmservice: CrmService,private commonService : CommonService,
    private route: ActivatedRoute,) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe((p) => {
      this.editleadId = p.get("id");
    });
    console.log("this.leadId",this.editleadId)
    this.geteditLeadById();
    this.gettaskList();
    this.commonService.rowAdded$.subscribe(() => {
      this.gettaskList();
    });
  }

  
  geteditLeadById() {
    this.crmservice.getleadById({}, this.editleadId).then((res) => {
      console.log("res taskwala",res);
      this.name=res.data.name;
    })
  }

  gettaskList() {
    this.crmservice.getTaskList({}, this.editleadId,this.search).then((res) => {
      console.log("res task view",res);
      this.tasklist = res.data.pageData;
    })
  }

  
  getsearchtaskList(searchKey){
    this.crmservice.getTaskListsearch({},this.editleadId,this.search,this.partyPageSize, (this.currentPageNo + 1),this.searchKey).then((res) => {
      console.log("search note",res);
      this.tasklist = res.data.pageData;

    })
  }
  openEditTaskModel(editleadId,taskId){
    const dialogRef: MatDialogRef<UpdateTaskComponent> = this.dialog.open(UpdateTaskComponent, {
      width: '700px',
      height: '730px',
      data: { editleadId: editleadId,taskId:taskId },
      panelClass: 'custom-dialog-task-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }

  openDialogtask(editleadId): void {
    const dialogRef: MatDialogRef<AddTaskComponent> = this.dialog.open(AddTaskComponent, {
      width: '700px',
      height: '730px',
      data: {editleadId:editleadId},
      panelClass: 'custom-dialog-task-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

}
