import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AddLeadComponent } from '../add-lead/add-lead.component';
import { CrmService } from 'src/app/core/services/crm.service';
import { EditLeadComponent } from '../edit-lead/edit-lead.component';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { Router } from '@angular/router';
import { TitleCasePipe } from '@angular/common';

@Component({
  selector: 'app-lead-list',
  templateUrl: './lead-list.component.html',
  styleUrls: ['./lead-list.component.scss']
})
export class LeadListComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public currentPageNo: number = 0;
  public partyPageSize: number = 10;
  public leadlistData:any;
  public deleletLeadId: any;
  public checkSelect: boolean = false;
  public allSelect: boolean = false;
  public archiveIconfalse: boolean = false;
  public checkSingleArray: any = [];
  public upiCheckedId = [];
  public checkSelectsinglefalse: boolean = false;
  public isChecked: boolean = false;
  public upiCheckedId1 = [];
  public productId: any;
  public searchKey: any;
  public excelFileUpload: any;

  constructor(public dialog: MatDialog,
    public crmservice:CrmService,
    private toastService: ToastNotificationService,
    private router: Router,private titleCasePipe: TitleCasePipe,) {
    
   }

  ngOnInit(): void {
    this.getLeadList();
    this.crmservice.leadAdded$.subscribe(() => {
      // Fetch the updated list of users
      this.getLeadList();
    });
  }

  getLeadList(){
    this.crmservice.getAllLeadList({}, this.currentUser.id,this.partyPageSize, (this.currentPageNo + 1)).then((res) => {
      console.log("lead list data",res);
      this.leadlistData = res.data.pageData;

    })
  }

  getsearchLead(searchKey) {
    console.log("****",this.searchKey);

    if (this.searchKey == "") {
      this.toastService.toastMsg({
        title: "Error",
        content: "Please Enter Valid Search Name, Email, Mobile Number!!!",
      });
      // this.toastService.openErrorSnackBar("Please Enter Valid search Name, Email, Mobile Number!!!");
      this.crmservice.getsearchAllLeadList({}, this.currentUser.id,this.partyPageSize, (this.currentPageNo + 1),this.searchKey).then((res) => {
        console.log("lead list data search",res);
        this.leadlistData = res.data.pageData;
      })
    }
    else {
      this.crmservice.getsearchAllLeadList({}, this.currentUser.id,this.partyPageSize, (this.currentPageNo + 1),this.searchKey).then((res) => {
        console.log("lead list data search",res);
        this.leadlistData = res.data.pageData;
        // this.toastService.openSnackBar("Product Found Successfully!!!");
      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }

      }
      );

    }


  }

  viewlead(id:string){
    this.router.navigate(["/pages/crm/view-lead/" + id +"/activity/"+id]);
  }
  
  openDialog(): void {
    const dialogRef: MatDialogRef<AddLeadComponent> = this.dialog.open(AddLeadComponent, {
      width: '700px',
      height: '100vh',
      data: {},
      panelClass: 'custom-dialog-lead-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  updateLeadId(leadId: string) {
    //this.router.navigate(["/pages/product-update/" + id]);
    const dialogRef = this.dialog.open(EditLeadComponent, {
      width: '700px',
      height: '100vh',
      data: { productId: leadId },
      panelClass: 'custom-dialog-update-Leadclass',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe(result => {

    });

  }

  deleteLeadById(deleteleadId: any) {
    this.deleletLeadId = deleteleadId;
  }

  leaddeleteOfferById() {

    this.crmservice.deleteleadById({},this.deleletLeadId).then((res) => {
      // this.toastService.openSnackBar("Lead Deleted Successfully!!!");
      this.toastService.toastMsg({
        title: "Success",
        content: "Lead Deleted Successfully!!!"
      });
      this.getLeadList();
    })

  }

  handleInputChange(e) {
    this.excelFileUpload = e.target.files[0];
    if (this.excelFileUpload) {
      let formdata = new FormData();
      formdata.set("file", this.excelFileUpload)
      this.crmservice.postBulklead(formdata, this.currentUser.id).then((res) => {
        this.toastService.toastMsg({
          title: "Success",
          content: "Excel Uploaded Successfully!!!"
        });
        // this.toastService.openSnackBar("Excel Uploaded Successfully")
        this.getLeadList();
        window.location.reload()
      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else if (err.error.status == "409" && err.error.expose == false) {
          this.toastService.toastMsg({
            title: "Error",
            content: "List Of Product Already Exists",
          });
          // this.toastService.openErrorSnackBar("List of Product Already Exists");
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
      });
    }
  }
  startUpload() {
    document.getElementById('uploadFile').click();
  }

  onChekMultiple($event) {
    let id = $event.target.value;
    this.isChecked = $event.target.checked;
    console.log("this.isChecked", this.isChecked)
    this.upiCheckedId1 = this.leadlistData.map((item) => {
      if (id == -1) {
        if (this.isChecked == true) {
          this.archiveIconfalse = true;
          this.checkSelectsinglefalse = true;
          return item;
        }
        else {
          this.archiveIconfalse = false;
          this.checkSelectsinglefalse = false;
          this.upiCheckedId = [];
          this.upiCheckedId1=[];
        }
      }
    })
    console.log("multiple click", this.upiCheckedId1)
    for (var i = 0; i < this.upiCheckedId1.length; i++) {
        this.upiCheckedId.push(this.upiCheckedId1[i].id);
    }
    console.log("multiple click", this.upiCheckedId)

  }

  getUPIcheckId(e, productId) {
    this.productId = productId;
    console.log("this.productId",this.productId)
    


  }

  getSingleCheckedId(e, productId) {
    this.productId = productId;

  }

  onChekedsingleCheckfalse(e, id) {
    console.log("checked", e.target.checked)

    if (e.target.checked == true) {
      this.archiveIconfalse = true;
      this.checkSingleArray.push(e.target.value);

    }
    else {
      if (this.upiCheckedId.length > this.checkSingleArray.length) {

        var index1 = this.upiCheckedId.indexOf(id);
        console.log(index1)
        this.upiCheckedId.splice(index1, 1);
        console.log("after single click upiCheckedId", this.upiCheckedId)

        if (this.upiCheckedId.length > 0) {
          this.archiveIconfalse = true;
          this.checkSelect = false;
          this.isChecked=false;
        }
        else {
          this.archiveIconfalse = false;
          this.upiCheckedId = [];
        }

      }
      else {
        var index = this.checkSingleArray.indexOf(id);
        console.log(index)
        this.checkSingleArray.splice(index, 1);
        console.log("this.checkSingleArray", this.checkSingleArray)

        if (this.checkSingleArray.length > 0) {
          this.archiveIconfalse = true;
        }
        else {
          this.archiveIconfalse = false;
          this.checkSingleArray = [];
        }

      }
    }
  }

  deleteProductById() {
    console.log("this.productId-->",this.productId)
    let request = {
      "crmId": this.productId

    }

    this.crmservice.deletemultileadById(request, this.currentUser.id).then((res) => {
      if (res) {
        // this.goodsData = res.data.pageData;
        this.toastService.toastMsg({
          title: "Success",
          content: "Lead Deleted Successfully!!!"
        });
        // this.toastService.openSnackBar("Lead Deleted Successfully!!!");
        this.archiveIconfalse = false;
        this.allSelect = false;
        this.checkSelectsinglefalse = false;
        this.isChecked=false;
        this.upiCheckedId = [];
        this.checkSingleArray = [];
       
        this.getLeadList(); 
      }

    }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
          // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
          // this.toastService.openErrorSnackBar("Something Went Wrong.");
        }

      }
    );
  }

 
}
