import { Component, Inject, OnInit } from '@angular/core';
// ckeditor
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
import { DomSanitizer } from '@angular/platform-browser';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { AddTaskComponent } from '../add-task/add-task.component';
import { TitleCasePipe } from '@angular/common';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { CrmService } from 'src/app/core/services/crm.service';

import * as moment from 'moment';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { CommonService } from 'src/app/core/services/common.service';
const editorContent = ``;
let editorConfig = {
  ui: 'pt',
  // language: 'pt',
  toolbar: {
    items: [
      'alignment',  // Displaying the proper UI element in the toolbar.
      'heading',
      '|',
      'bold',
      'italic',
      'link',
      'bulletedList',
      'numberedList',
      '|',
      'outdent',
      'indent',
      '|',
      'imageUpload',
      'blockQuote',
      'insertTable',
      'mediaEmbed',
      'undo',
      'redo'
    ]
  },
  language: 'en',
  image: {
    toolbar: [
      'imageTextAlternative',
      'toggleImageCaption',
      'imageStyle:inline',
      'imageStyle:block',
      'imageStyle:side'
    ]
  },
  table: {
    contentToolbar: [
      'tableColumn',
      'tableRow',
      'mergeTableCells'
    ]
  }
};

@Component({
  selector: 'app-update-email',
  templateUrl: './update-email.component.html',
  styleUrls: ['./update-email.component.scss']
})
export class UpdateEmailComponent implements OnInit {

  ckeditor = ClassicEditor;
  editorConfig = editorConfig;
  output = null;
  editorContent = editorContent;
  AddTaskForm: FormGroup;
  editleadId: any;
  public leadId: any;
  public taskId: any;
  userData: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  today: Date;

  constructor(public dialogRef: MatDialogRef<AddTaskComponent>, private formBuilder: FormBuilder, @Inject(MAT_DIALOG_DATA) public taskData: any, private toastService: ToastNotificationService, private crmService: CrmService,
    private commonService: CommonService,
    private titleCasePipe: TitleCasePipe, public rolesPermissionsService: RolesPermissionsService,) { }

  ngOnInit(): void {
     this.today=new Date();
    this.leadId = this.taskData.editleadId;
    this.taskId = this.taskData.taskId;
    this.getTaskDataByleadId();

    console.log("leadId", this.leadId)

    this.AddTaskForm = this.formBuilder.group({
      //task: ['', Validators.required],
      taskdate: ['', Validators.required],
      taskTime: [''],
      taskReminder: [''],
      taskType: ['', Validators.required],
      taskPrioirty: ['', Validators.required],
      // taskQue: ['',],
      taskAssignedTo: ['', Validators.required],
      taskNote: ['', Validators.required],
    });
  }
  getUsersList() {
    this.rolesPermissionsService.getUsers({}, this.currentUser.id,1000,1).then((res) => {
      this.userData = res.data.pageData.map((item) => { return item.firstName });

    })
  }
  getTaskDataByleadId() {
    this.crmService.getCrmDATABYLEADID({}, this.leadId, this.taskId,).then((res: any) => {

      this.AddTaskForm.patchValue({
        //task: res.data.taskName,
        taskdate: moment(res.data.createdAt).format("DD-MMM-YYYY"),
        taskTime: res.data.updatedAt ? res.data.updatedAt : "",
        taskReminder: "",
        taskType: res.data.type,
        taskPrioirty: res.data.priority,
        //taskQue: "",
        taskAssignedTo: res.data.assignedTo,
        taskNote: res.data.taskDescription,

      })
    })

  }

  clearInput() {
    this.dialogRef.close();
  }
  //ckeditor

  onChange({ editor }: ChangeEvent) {
    this.editorContent = editor.getData();
  }
  AddTask() {
    if (this.AddTaskForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {
      let Body =
      {

        "taskName": "Buy Groceriesgit",
        // "dueDate": moment(this.AddTaskForm.controls.taskdate.value, 'DD-MMM-YYYY').format('YYYY-MM-DD'),
        "actualDate": moment(this.AddTaskForm.controls.taskdate.value, 'DD-MMM-YYYY').format('YYYY-MM-DD'),
        "remainder": this.AddTaskForm.controls.taskReminder.value,
        "type": this.AddTaskForm.controls.taskType.value,
        "priority": this.AddTaskForm.controls.taskPrioirty.value,
        //"queue": this.AddTaskForm.controls.taskQue.value,
        "assignedTo": this.AddTaskForm.controls.taskAssignedTo.value,
        "taskDescription": this.AddTaskForm.controls.taskNote.value,
        //  "images": []

      }

      this.crmService.updateEmail(Body, this.taskId).then(res => {
        this.commonService.notifyDataAdded()
        this.toastService.toastMsg({
          title: "Success",
          content: "Email Updated Successfully!!!"
        });
        // this.toastService.openSnackBar("Email Updated Successfully!!!");
        this.dialogRef.close();

      }
        , (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
            // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
          }
          else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went Wrong.",
            });
            // this.toastService.openErrorSnackBar("Something Went Wrong.");
          }
        })


    }

  }
  openDatepicker() {
    // Check if the current value of taskdate is an "Invalid Date"
    const selectedDate = this.AddTaskForm.get('taskdate').value;
    if (isNaN(selectedDate.getTime())) {
      // Set a default date or any valid date here
      this.AddTaskForm.get('taskdate').setValue(new Date());
    }
  }


}
