import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { CrmService } from 'src/app/core/services/crm.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

// ckeditor
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular/ckeditor.component';
import { DomSanitizer } from '@angular/platform-browser';
import { CommonService } from 'src/app/core/services/common.service';

const editorContent = ``;

let editorConfig = {
  ui: 'pt',
  // language: 'pt',
  toolbar: {
    items: [
      'alignment',  // Displaying the proper UI element in the toolbar.
      'heading',
      '|',
      'bold',
      'italic',
      'link',
      'bulletedList',
      'numberedList',
      '|',
      'outdent',
      'indent',
      '|',
      'imageUpload',
      'blockQuote',
      'insertTable',
      'mediaEmbed',
      'undo',
      'redo'
    ]
  },
  language: 'en',
  image: {
    toolbar: [
      'imageTextAlternative',
      'toggleImageCaption',
      'imageStyle:inline',
      'imageStyle:block',
      'imageStyle:side'
    ]
  },
  table: {
    contentToolbar: [
      'tableColumn',
      'tableRow',
      'mergeTableCells'
    ]
  }
};

@Component({
  selector: 'app-add-note',
  templateUrl: './add-note.component.html',
  styleUrls: ['./add-note.component.scss']
})
export class AddNoteComponent implements OnInit {
  ckeditor = ClassicEditor;
  editorConfig = editorConfig;
  output = null;
  editorContent = editorContent;

  noteForm: FormGroup;
  editleadId: any;
  public leadId: any;

  constructor(@Inject(MAT_DIALOG_DATA) public noteData: any, private toastService: ToastNotificationService, private crmService: CrmService,
    private titleCasePipe: TitleCasePipe, private formBuilder: FormBuilder, public dialogRef: MatDialogRef<AddNoteComponent>,
    private route: ActivatedRoute, private domSanitizer: DomSanitizer, private commonService : CommonService) { }

  ngOnInit(): void {
    this.leadId = this.noteData.editleadId;
    console.log("leadId", this.leadId)

    this.noteForm = this.formBuilder.group({
      note: ['', Validators.required]
    });
  }

  clearInput() {
    this.dialogRef.close();
  }
  //ckeditor

  onChange({ editor }: ChangeEvent) {
    this.editorContent = editor.getData();
  }

  submit() {
    this.output = this.domSanitizer.bypassSecurityTrustHtml(this.editorContent);
  }
  //ckeditor
  SaveNote() {
    if (this.noteForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please Fill Mandatory Fields!!!");
      return false;
    }
    else {
      let Body =
      {
        "note": this.noteForm.controls.note.value,
        "images": []

      }
      this.crmService.postNote(Body, this.leadId).then(res => {
        this.commonService.notifyDataAdded()
        this.toastService.toastMsg({
          title: "Success",
          content: "Note Added Successfully!!!"
        });
        // this.toastService.openSnackBar("Note Added Successfully!!!");
        this.dialogRef.close();

      }
        , (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
            // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
          }
          else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went Wrong",
            });
            // this.toastService.openErrorSnackBar("Something Went Wrong.");
          }
        })


    }



  }


}
